.footer-buttons {
  position: fixed;
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - 24px);
  max-width: 480px;
  background: rgba(18, 18, 18, 0.9);
  backdrop-filter: blur(14px);
  border-radius: 24px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 0;
  z-index: 1000;
  overflow: hidden;
  transition: all 0.3s ease;
}

.floating-indicator {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 33.3333%;
  height: 3px;
  background: linear-gradient(90deg, #6FCF00 0%, #3DFF76 100%);
  transition: transform 0.3s ease;
  border-radius: 2px 2px 0 0;
  z-index: 0;
}

.menu-button {
  background: none;
  border: none;
  flex: 1;
  color: #bdbdbd77;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0.15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 12px 0 8px;
  cursor: pointer;
  transition: color 0.3s ease, transform 0.15s ease;
  position: relative;
  z-index: 1;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.6);
}

.menu-button .icon-wrapper {
  background: transparent;
  border-radius: 50%;
  padding: 6px;
  height: 32px;
  width: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease;
}

.menu-button .menu-icon {
  font-size: 22px;
  line-height: 1;
  display: block;
}

.menu-button.active {
  color: #ddd;
}

.menu-button.active .icon-wrapper {
  background: rgba(111, 207, 0, 0.1);
}

.menu-button:focus {
  outline: none;
}

.menu-button span {
  margin-top: 2px !important;
  font-size: 12px;
  line-height: 1.3;
  font-weight: 500;
  color: inherit;
  display: block;
  text-align: center;
  white-space: nowrap;
  pointer-events: none;
  user-select: none;
}