/* === Общее оформление страницы === */
body {
  margin: 0;
  background: #121212; /* Тёмный фон */
  font-family: -apple-system, BlinkMacSystemFont, ...;
  color: #e0e0e0; /* Светлый текст */
  font-size: 13px;
  line-height: 1.4;
  letter-spacing: 0.2px;
}

/* === Обёртка всей системы Otes === */
.otes-wrapper {
  
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  user-select: none;
  pointer-events: auto;
  -webkit-touch-callout: none;
}
.otes-wrapper * {
  user-select: none !important;
  -webkit-touch-callout: none !important;
}

/* === Заголовки (месяц, цели) === */
.otes-title,
.otes-date-left {
  padding: 6px 22px 6px;         /* точно так же, как в .calendar-header */
  font-size: 16px;
  font-weight: 800;
  margin: 0;                     /* убираем bottom margin */
  color: #fff;
  border-bottom: 1px solid #222;
  text-align: left;
  text-shadow: none;            /* убираем ненужное свечение */
}

/* Если .otes-title не должна иметь линию — оставляем исключение */
.otes-title {
  border-bottom: none;
}

/* === Сетка карточек === */
.otes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 14px;
}

/* === Общая карточка === */
.otes-card {
  top: 10px;
  position: relative;
  background: rgba(30, 30, 30, 0.4);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  padding: 10px;
  margin: 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform 0.2s, box-shadow 0.2s;
}
.otes-card:first-child .otes-card-title {
  border: none !important;
}

/* === Заголовок карточки (например: "Зарплата") === */
.otes-card-title {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 0.95rem;
  padding-bottom: 3px;
  margin-bottom: -6px;
  border-bottom: 1px solid #ffffff22;
}

/* === Основные значения внутри карточек === */
.otes-value {
  font-size: 1.4rem;
  font-weight: bold;
  margin-top: 8px;
}
.unit {
  font-size: 0.65em;
  opacity: 0.6;
  margin-left: 4px;
}

/* === Цвета: жёлтый, синий, красный === */
.otes-yellow { color: #FFD54F; }
.otes-blue   { color: #4FC3F7; }
.otes-red    { color: #EF5350; }

/* === Прогресс-бары целей === */
.otes-progress {
  width: 100%;
  height: 10px;
  margin-top: 8px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
}
.otes-progress-bar {
  height: 100%;
  border-radius: 8px;
  transition: width 0.4s ease-in-out;
}
.otes-progress-bar.otes-yellow { background: linear-gradient(90deg, #facc15, #fde047); }
.otes-progress-bar.otes-green  { background: linear-gradient(90deg, #22c55e, #86efac); }
.otes-progress-bar.otes-red    { background: linear-gradient(90deg, #ef4444, #f87171); }

.otes-progress-info {
  margin-top: 8px;
  font-size: 12px;
}

/* === Значки статуса (Успех, Провал и т.д.) === */
.otes-status-badge {
  margin-top: 10px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-size: 0.75rem;
  font-weight: bold;
  padding: 4px 8px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.08);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.otes-status-badge.otes-grey {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.4);
  padding: 3px 8px;
  font-size: 11px;
  margin-top: 6px;
}

/* === Цитата мотивации === */
.otes-quote {
  margin-top: 30px;
  font-size: 0.85rem;
  font-style: italic;
  color: rgba(255, 255, 255, 0.4);
  text-align: center;
  letter-spacing: 0.5px;
}

/* === Группа инпутов (для целей) === */
.otes-inputs {
  margin-bottom: 24px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 16px;
}
.otes-input-group {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-top: 10px;
}
.otes-input-group label {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
}
.otes-input-group input {
  padding: 8px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.07);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.15);
  font-size: 0.9rem;
  transition: 0.2s;
}
.otes-input-group input:focus {
  border-color: #6FCF00;
  background: rgba(255, 255, 255, 0.1);
}

/* === Навигация по месяцам === */
/* === Навигация по месяцам (в том же духе, что .calendar-header) === */
.otes-date-range {
  display: flex;
  align-items: center;
  padding: 0px 16px 0px;
  font-size: 12px;
  font-weight: 500;
  border-bottom: 1px solid #222;
  gap: 0px;
  margin: 0;  /* убрали отрицательный отступ */
  color: #999;
  position: relative;
}

/* Поднимаем содержимое (текст + стрелки) внутри .otes-date-range */
.otes-date-range > * {
  position: relative;
  top: -6px; /* поднимаем только содержимое */
}

/* === Кнопки стрелок — чистим и выравниваем === */
.otes-arrow-button,
.otes-nav-buttons button {
  background: none;
  border: none;
  color:#999;
  cursor: pointer;
  padding: 3px 0px;
  border-radius: 6px;
  transition: background-color 0.2s ease;
}

.otes-arrow-button {
  position: relative;
  top: -5px; /* 🔻 Чуть-чуть опустили стрелочки */
}

/* === Анимированная панель для ввода целей === */
.otes-slide-panel {
  position: relative;
  max-height: 0;
  opacity: 0;
  transform: translateY(-10px);
  transition: 0.4s ease;
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.otes-slide-panel.open {
  max-height: 1000px;
  opacity: 1;
  transform: translateY(0);
}

/* === Кнопка "+" и модальное окно выбора целей === */
.otes-add-tip-btn {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 18px;
  display: flex;
  align-items: center;
  gap: 6px;
}
.otes-plus-icon {
  font-size: 36px;
}

/* === Модальное окно и затемнение === */
.otes-overlay {
  position: fixed;
  inset: 0;
  background: rgba(30, 30, 30, 0.822);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.otes-modal {
  position: relative; /* 💥 Вот это добавь */
  background: rgb(30, 30, 30);
  border-radius: 20px;
  padding: 18px 26px;
  max-width: 400px;
  width: 60%;
  text-align: center;
  color: #e2e8f0;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.4);
}
.otes-modal h3 {
  font-size: 18px;
  margin-bottom: 16px;
  color: #f1f5f9;
}
.otes-modal-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.otes-modal-buttons button {
  padding: 10px 16px;
  font-size: 14px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.05);
  color: #f1f5f9;
  cursor: pointer;
  transition: 0.2s;
  border: none;
}
.otes-modal-close {
  position: absolute;
  top: 10px;
  right: 12px;
  font-size: 20px;
  color: #fff;
  background: none;
  border: none;
  cursor: pointer;
}

/* === Крестик на карточке для удаления === */
.otes-close-card {
  position: absolute;
  top: 12px;
  right: 8px;
  background: transparent;
  border: none;
  color: #fff;
  font-size: 18px;
  cursor: pointer;
  z-index: 10;
}

/* === Мини-статистика внизу карточек === */
.otes-stats-row {
  display: flex;
  justify-content: space-between;
  margin-top: 8px;
  padding-top: 4px;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
}
.otes-stat {
  display: flex;
  flex-direction: column;
}
.otes-stat-label {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
  margin-bottom: 2px;
}
.otes-stat-value {
  font-size: 1rem;
  font-weight: 600;
  color: #e0e0e0;
}
.otes-stat-value.otes-green { color: #22c55e; }
.otes-stat-value.otes-red   { color: #ef4444; }

/* === Цели справа в карточке === */
.otes-goal-inline {
  margin-left: auto;
  margin-top: 10px;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 0.85rem;
  color: #f0f0f0;
  font-weight: 500;
  text-align: center;
  animation: fadeIn 0.4s ease-in-out;
}
.otes-goal-label {
  color: #aaa;
  margin-right: 6px;
}
.otes-goal-value {
  font-size: 0.9em;
  opacity: 0.6;
  font-weight: 600;
  margin-left: -4px;
}

