/* === Общие стили для body === */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background-color: #121212;
  color: #ffffff;
  overflow: hidden;
  height: 100vh;
}

/* === Обёртка всего календаря === */
.main-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* === Заголовок календаря === */
.calendar-header {
  padding: 6px 22px 0px;
  border-bottom: 1px solid #222;
}

.calendar-header h2 {
  font-size: 16px;
  font-weight: 800;
  margin: 0;
}

.calendar-header p {
  font-size: 12px;
  color: #999;
  margin-top: 0px;
}

/* === Контейнер с неделями === */
.week-container {
  flex-grow: 1;
  position: relative;
  overflow-y: auto; /* вертикальный скролл, если не влезает */
  padding: 0 16px 16px;
  box-sizing: border-box;
}

.week-block {
  position: relative; /* больше не абсолют */
  width: 100%;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  background: rgba(30, 30, 30, 0.4);
  border-radius: 20px;
  padding: 0 0 12px 0;
  box-shadow: 0 4px 15px #000000cc;
  box-sizing: border-box;
}

/* Вертикальная линия */
.week-block::before {
  content: '';
  position: absolute;
  left: 76px;   /* фиксированное место для линии */
  top: 0px;
  bottom: 20px;
  width: 1px;
  background-color: #444;
  border-radius: 2px;
  z-index: 1;
}

/* Один день недели */
.day-row {
  position: relative;
  height: 56px; /* фиксированная, одинаковая высота */
  margin: 0;
  padding: 0 16px 0 12px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 12px;
  cursor: pointer;
  box-sizing: border-box;
}

/* Горизонтальная линия */
.day-row::after {
  content: '';
  position: absolute;
  left: 60px; /* под вертикальной */
  right: 16px;
  bottom: -1px; /* НАД следующим блоком */
  height: 1px;
  background-color: #444;
  border-radius: 1px;
  z-index: 1;
}

/* Линия не последнему дню */
.day-row:last-child::after {
  display: none; /* убираем последнюю разделительную */
}


/* Левая часть с днем и датой */
.day-left {
  flex: 0 0 45px; /* фиксированная ширина */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  user-select: none;
  padding-left: 6px;
  box-sizing: border-box;
  text-align: center;
}

/* === Название дня === */
.day-name {
  color: #888;           /* Сделали цвет потемнее и менее ярким */
  font-weight: 600;
  font-size: 10px;       /* Уменьшили размер с 14px до 10px */
  line-height: 1.2;
  padding: 0;
  text-align: center;
  user-select: none;
}

/* === Дата под названием === */
.day-date {
  font-size: 11px;
  color: #ddd;
  line-height: 1.2;
  padding: 0px 6px;
  text-align: center;
}

/* === Выделение сегодняшнего дня === */
.current-day .day-date {
  border: 1px solid #fff;
  background-color: #fff;
  border-radius: 50px;
  font-weight: 700;
  color: #000;
  padding: 2px 10px;
}

/* Поле смен и событий */
.day-events {
  flex-grow: 1;
  margin-left: -30px;      /* расстояние между левой частью и полем */
  padding-left: 56px;     /* чтобы текст начинался справа от вертикальной линии */
  color: #777;
  font-size: 13px;
  user-select: none;
  min-height: 40px;
  border-radius: 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}

.modal-overlay {
  position: fixed;
  inset: 0;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  z-index: 3000;
  pointer-events: auto;
  user-select: none;
}

.choice-modal {
  margin-top: 45px;
  position: absolute;
  background: #eee;
  color: #333;
  border-radius: 16px;
  padding: 18px 12px 14px 12px;
  display: flex;
  align-items: center;
  gap: 16px;
  box-shadow: 0 0 12px #00000033;
  min-width: 280px;
  max-width: 90vw;
  user-select: none;
  pointer-events: auto;
  z-index: 9999; /* на всякий случай выше всего */
}


.choice-icon {
  position: absolute;
  top: -15px;
  left: 16px;
  font-size: 20px;
  padding: 4px;
  color: #eee;
  z-index: 2;
}

.choice-btn {
  flex: 1;
  background: transparent;
  border: none;
  font-size: 15px;
  display: flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
  padding: 8px 16px;
  border-radius: 12px;
  transition: background-color 0.3s ease;
}

.plus {
  font-weight: 900;
  font-size: 12px;
  line-height: 1;
}

.choice-divider {
  width: 1px;
  height: 28px;
  background: #bbb;
}

.sun-icon-wrapper {
  position: relative;
  font-size: 24px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fcd34d; /* мягкий желтоватый */
}

.sun-icon {
  font-size: 32px;
  filter: drop-shadow(0 0 4px rgba(255, 255, 255, 0.5));
}

.sun-day-number {
  position: absolute;
  font-size: 12px;
  font-weight: bold;
  color: #000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

@media (max-width: 480px) {
  .week-container {
    padding: 0 12px 12px;
  }

  .day-row {
    height: 52px;
    gap: 8px;
  }

  .day-name, .day-date {
    font-size: 10px;
  }

  .day-events {
    font-size: 12px;
  }
}


